<template>
    <section class="layout">

        <section v-if="hasWaybills">
            <div class="list">
                <div class="item" v-for="item in activeWaybills" :key="item.id" v-on:click="itemClickHandler(item)">

                    <div class="head">
                        <p class="title">#{{item.numberInvoice}}</p>
                        <p class="delivery-date" v-if="item.deliveryDate">{{item.deliveryDate}}</p>
                        <p class="delivery-date" v-else>Резерв</p>
                    </div>

                    <div class="content">
                        <p class="is-paid is-paid-true" v-if="item.isPaid">Оплачен</p>
                        <p class="is-paid is-paid-false" v-if="!item.isPaid">Не оплачен</p>
                        <p class="total-price">{{item.totalPrice}} ₴</p>
                    </div>
                </div>
            </div>

            <section v-if="inactiveWaybills.length !== 0">
                <p class="inner-list-text">Доставленные</p>

                <div class="list inactive">
                    <div class="item" v-for="item in inactiveWaybills" :key="item.id" v-on:click="itemClickHandler(item)">

                        <div class="head">
                            <p class="title">#{{item.numberInvoice}}</p>
                            <p class="delivery-date" v-if="item.deliveryDate">{{item.deliveryDate}}</p>
                            <p class="delivery-date" v-else>Резерв</p>
                        </div>

                        <div class="content">
                            <p class="is-paid is-paid-true" v-if="item.isPaid">Оплачен</p>
                            <p class="is-paid is-paid-false" v-if="!item.isPaid">Не оплачен</p>
                            <p class="total-price">{{item.totalPrice}} ₴</p>
                        </div>

                        <div class="blur"></div>
                    </div>
                </div>
            </section>
        </section>

        <div class="bottom" v-observe-visibility="onBottomReached">
            <LoadingSpinner v-if="isLoading"></LoadingSpinner>
        </div>

    </section>

</template>

<script>
    import { mapGetters, mapActions } from 'vuex';
    import modules from '../store/all.modules.name';
    import actions from "../store/all.actions.type";
    import LoadingSpinner from "./components/LoadingSpinner";

    import Vue from 'vue'
    import VueObserveVisibility from 'vue-observe-visibility'

    Vue.use(VueObserveVisibility);

    export default {
        name: "Waybills",

        components: {LoadingSpinner},

        data: () => {
            return {
            }
        },

        methods: {
            ...mapActions(modules.waybills, [
                actions.waybills.LOAD_WAYBILLS,
                actions.waybills.LOAD_MORE_WAYBILLS,
            ]),

            itemClickHandler(item) {
                this.$router.push({ name: 'waybill', params: { id: item.id } })
            },

            onBottomReached(isVisible) {
                if (!this.isLoading && isVisible && this.hasWaybills && this.hasMoreToLoad) {
                    this.loadMoreWaybills();
                }
            }
        },

        computed: {
            ...mapGetters(modules.waybills, [
                'activeWaybills',
                'inactiveWaybills',
                'hasWaybills',
                'hasMoreToLoad',
                'isLoading'
            ]),
        },

        created() {
            if (!this.hasWaybills) {
                this.loadWaybills();
            }
        },
    }
</script>

<style lang="sass" scoped>

    .layout
        width: 90%
        max-width: 500px
        margin: auto
        font-family: Roboto, sans-serif

        .bottom
            display: flex
            justify-content: center
            height: 100px

        .inner-list-text
            text-align: center
            margin-bottom: 0.5rem
            margin-top: 2rem
            font-size: 1.15rem

        .list

            .item
                cursor: pointer
                margin-top: 20px
                margin-bottom: 20px
                padding-left: 10px
                padding-right: 10px
                //height: 50px
                transition: 0.35s
                box-shadow: 0 0 3px rgba(0, 0, 0, 0.5)
                border-radius: 5px

                &:hover
                    box-shadow: 0 0 6px 1px rgba(0, 0, 0, 0.5)

                p
                    display: inline-block
                    margin-top: 0.5em
                    margin-bottom: 0.5em


                .head
                    display: flex

                    .title
                        font-weight: bold
                        font-size: 18px

                    .delivery-date
                        margin-left: auto
                        color: #777777
                        font-size: 15px


                .content
                    display: flex

                    .total-price
                        margin-left: auto
                        font-size: 17px

                    .is-paid-true
                        color: #81C784

                    .is-paid-false
                        color: #e57373

        .inactive
            border-top: 2px solid #bababa

            .item
                position: relative
                box-shadow: 0 0 2px rgba(0, 0, 0, 0.5)

                &:hover
                    box-shadow: 0 0 4px 0px rgba(0, 0, 0, 0.5)

                .blur
                    position: absolute
                    pointer-events: none
                    height: 100%
                    top: 0
                    right: 0
                    left: 0
                    background-color: #ffffffa1
</style>